import React, { useState } from 'react'
import css from "../css/settings.module.css"
import cookies from "cookie-universal"

const Settings = () => {
    const Cookies = new cookies()

    const displayType = useState(Cookies.get("displaytype"))

    return (
        <div className={`container ${css.settings}`}>
            <h3>ANG TRONG QU TRNH PHT TRIN, S C SAU</h3>
            <div>Chn kiu hin th ca bn: <select>
                <option>Hin th kiu l bi</option>
                <option defaultChecked={displayType === "ng" ? true : false}>Hin th kiu nm ngang</option>
            </select></div>
        </div>
    )
}

export default Settings