import React, { useEffect, useState } from 'react'
import { useQuill } from 'react-quilljs'
import "quill/dist/quill.snow.css"
import css from "../css/editor.module.css"
import axios from 'axios'

const Editor = () => {
    const { quill, quillRef } = useQuill()
    const [ value, setValue ] = useState("")
    
    useEffect(() => {
        const handle = () => {
            setValue(quillRef.current.firstChild.innerHTML)
        }
        if (quill)
            quill.on("text-change", handle)
        return () => {
            if (quill)
                quill.off("text-change", handle)
        }
    }, [quill, quillRef])

    const save = () => {
        const file = new Blob([value], { type: 'text/plain' });
        const link = document.createElement("a")
        link.href = URL.createObjectURL(file)
        link.download = `${Date.now()}-MCWIKI.txt`
        link.click()
        link.remove()
        URL.revokeObjectURL(link.href);
    }

    const open = (e) => {
        const reader = new FileReader()
        const url = URL.createObjectURL(e.target.files[0])
        reader.onerror = err => console.log(err)
        reader.onload = function(fileLoadedEvent){
            var textFromFileLoaded = fileLoadedEvent.target.result;
            console.log(textFromFileLoaded)
        };
        axios.get(url)
        .then(async res => {
            quillRef.current.firstChild.innerHTML = res.data
        })
        .catch(err => console.log(err))

        URL.revokeObjectURL(url)
    }

    return (
        <div>
            <div className={css.quill}>
                <div>
                    <div ref={quillRef}></div>
                </div>
            </div>
            <div className={css.save}>
                <label>
                    <span className={css.open}>M</span>
                    <input style={{ display: "none" }} onChange={open} type='file'/>
                </label>
                <button onClick={save}>LU</button>     
            </div>
        </div>
    )
}

export default Editor