import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import "quill/dist/quill.snow.css"

const WikiViewer = () => {
    const params = useParams()
    const [ content, setContent ] = useState("")

    useState(() => {
        document.title = `MCWIKI | ${params.id}`
    }, [])

    useEffect(() => {
        axios.get(`https://raw.githubusercontent.com/MineHighVN/wikisource/main/${`${params.topic}/${params.id}`}`)
        .then(res => {
            setContent(res.data)
        })
        .catch(err => {
            console.log(err)
        })
    }, [params])

    return (
        <div>
            <div className='container ql-editor' dangerouslySetInnerHTML={{__html: content}}></div>
        </div>
    )
}

export default WikiViewer