import React, { useEffect, useRef, useState } from 'react'
import Home from '../component/Home'
import css from "../css/navbar.module.css"
import { Link } from 'react-router-dom'
import Wiki from "../component/Wiki"
import Generate from "../component/Generate"
import Feedback from "../component/Feedback"
import Editor from '../component/Editor'
import HowToCreate from '../component/HowToCreate'
import WikiViewer from '../component/WikiViewer'
import Settings from '../component/Settings'

const Main = ({ route, view }) => {
    const [ showNav, setShowNav ] = useState(false)
    const menu = useRef()

    useEffect(() => {
        if (menu.current)
            for(const btn of menu.current.querySelectorAll("a"))
                btn.onclick = () => setShowNav(false)
    }, [menu])

    const body = (
        <>
            {route === "home" && <Home/>}
            {route === "wiki" && <Wiki/>}
            {route === "generate" && <Generate/>}
            {route === "feedback" && <Feedback/>}
            {route === "editor" && <Editor/>}
            {route === "howtocreate" && <HowToCreate/>}
            {route === "wikiviewer" && <WikiViewer/>}
            {route === "settings" && <Settings/>}
        </>
    )

    return (
        <>
            <nav>
                <div><h2>MCWIKI</h2></div>
                <div>
                    <i className={`${css.hamburger} fa-solid fa-bars`} onClick={() => setShowNav(true)}></i>
                    <div className={css.overlay} style={showNav ? { display : "block"} : {}} onClick={e => {if(e.currentTarget === e.target) setShowNav(false)}}></div>
                </div>
                <ul className={showNav ? css.showNav : ""} ref={menu}>
                    <li><Link className={view === "home" ? css.select : ""} to="/">Trang ch</Link></li>
                    <li><Link className={view === "wiki" ? css.select : ""} to="/wiki">Wiki</Link></li>
                    <li><Link className={view === "generator" ? css.select : ""} to="/generator">Genrator</Link></li>
                    <li><Link className={view === "settings" ? css.select : ""} to="/settings">Ci t</Link></li>
                    <li><Link className={view === "feedback" ? css.select : ""} to="/feedback">Lin h</Link></li>
                </ul>
            </nav>
            <div className={css.App}>
                {body}
            </div>
            <footer>
                <div className="container" style={{color: "white"}}>
                    <center><span>Copyright  2022-2023</span></center>
                </div>
                {/* <div className={css.row}><button id="changeTheme" value="dark">Dark</button></div> */}
            </footer>
        </>
    )
}

export default Main